import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/more/bg.png'
import learnBtnImage from '../assets/images/more/learn.png';
import endBtnImage from '../assets/images/more/end.png';

class More extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
    }
    
    navigateToLearnPage() {
        this.playBtnSound();
    };

    goToHomePage() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './'
        });
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img className='press-btn' alt='' src={learnBtnImage} style={{ height: '13%', left: '21%', bottom: '4.8%', position: 'absolute', background: 'transparent' }} onClick={this.navigateToLearnPage.bind(this)}/>
                <img className='press-btn' alt='' src={endBtnImage} style={{ height: '13%', right: '16%', bottom: '4.8%', position: 'absolute', background: 'transparent' }} onClick={this.goToHomePage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(More);
