import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/stage1-intro/bg.png'
import startBtnImage from '../assets/images/stage1-intro/start.png';

class Stage1Intro extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
    }
    
    startBtnClick() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './stage1-select'
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img className='press-btn' alt='' src={startBtnImage} style={{ height: '12%', bottom: '4%', left: '38.5%', position: 'absolute', background: 'transparent' }} onClick={this.startBtnClick.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Stage1Intro);
