import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/stage1-select/bg.png'
import ship1Btn from '../assets/images/stage1-select/ship-1.png'
import ship2Btn from '../assets/images/stage1-select/ship-2.png'
import ship3Btn from '../assets/images/stage1-select/ship-3.png'


class Stage1Select extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
        };
    }
    
    goToConfirmPage(index) {
        this.playBtnSound();
        this.props.history.push({
            pathname: './stage1-confirm',
            state: {
                shipIndex: index
            }
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img className='press-btn' alt='' src={ship1Btn} style={{ height: '40%', bottom: '27%', left: '14.5%', position: 'absolute', background: 'transparent' }} onClick={() => this.goToConfirmPage(0)}/>
                <img className='press-btn' alt='' src={ship2Btn} style={{ height: '40%', bottom: '27%', left: '39.5%', position: 'absolute', background: 'transparent' }} onClick={() => this.goToConfirmPage(1)}/>
                <img className='press-btn' alt='' src={ship3Btn} style={{ height: '40%', bottom: '27%', left: '64.5%', position: 'absolute', background: 'transparent' }} onClick={() => this.goToConfirmPage(2)}/>
            </div>
        )
    }
}

export default withRouter(Stage1Select);
