import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import ship1Image from '../assets/images/stage1-confirm/ship1-desc.png'
import ship2Image from '../assets/images/stage1-confirm/ship2-desc.png';
import ship3Image from '../assets/images/stage1-confirm/ship3-desc.png';
import reselectBtnImage from '../assets/images/stage1-confirm/re-select.png';
import confirmBtnImage from '../assets/images/stage1-confirm/confirm.png';

const descImage = [ ship1Image, ship2Image, ship3Image ];

class Stage1Confirm extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            backgroundImage: descImage[this.props.location.state.shipIndex]
        };
    }
    
    confirmBtnClick() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './stage1-game',
            state: {
                shipIndex: this.props.location.state.shipIndex
            }
        });
    };

    reselectBtnClick() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './stage1-select'
        });
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img className='press-btn' alt='' src={reselectBtnImage} style={{ height: '12%', bottom: '9%', right: '30%', position: 'absolute', background: 'transparent' }} onClick={this.reselectBtnClick.bind(this)}/>
                <img className='press-btn' alt='' src={confirmBtnImage} style={{ height: '12%', bottom: '9%', right: '5%', position: 'absolute', background: 'transparent' }} onClick={this.confirmBtnClick.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Stage1Confirm);
